}

/*-----------------------top nav ---------------------------*/

.background_container {
  width: 100%;
  height: 100vh;
  background: url(../img/pexels-david-besh-884788.jpg);
  background-position: center;
  background-size: cover;
  z-index: -1;
}

.top_nav {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(26, 26, 26, 0.631372549);
}

.top_nav .tel_number a {
  text-decoration: none;
  color: white;
  transition: 0.5s;
}

.top_nav a {
  text-decoration: none;
}

.top_nav a svg {
  width: 50px;
  transition: 0.5s;
}

.top_nav a:hover {
  color: #e3774c;
}

.top_nav .icons {
  padding-left: 20px;
}

.top_nav .icons svg:hover {
  fill: #e3774c;
}

.top_nav .tel_number {
  padding-right: 20px;
}

nav {
  width: 100%;
  height: 70px;
  background-color: rgba(26, 26, 26, 0.631372549);
  position: sticky;
  top: 0;
  left: 0;
  scroll-behavior: smooth;
}

nav .logo {
  z-index: 9999999;
}

nav .logo a {
  text-decoration: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 35px;
  font-family: "Alex Brush", cursive;
  color: #fff;
}

nav .logo a span {
  color: #e3774c;
}

nav .nav-links {
  justify-content: space-between;
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
}

nav .nav-links li a,
nav .nav-links .dropbtn {
  display: inline-block;
  color: white;
  text-align: center;
  text-decoration: none;
  transition: 0, 5s;
  letter-spacing: 1px;
  font-family: "Ingrid Darling", cursive;
}

nav .nav-links li.dropdown {
  display: inline-block;
}

nav .nav-links .dropdown-content {
  display: none;
  position: absolute;
  margin-left: -16px;
  background-color: #e3774c;
  margin-top: 22.5px;
  min-width: 120px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  transition: 0, 5s;
  transform: translate(120px, -70px);
}

nav .nav-links .dropdown-content a {
  color: #fff;
  padding: 7px 11px;
  text-decoration: none;
  display: block;
  text-align: left;
}

nav .nav-links .dropdown-content a:hover {
  background-color: #f1f1f1;
  color: #e3774c;
}

nav .nav-links .dropdown:hover .dropdown-content {
  color: #e3774c;
  transition: 0, 5s;
  display: block;
}

nav .nav-links ul {
  font-family: NSARegular, sans-serif;
  font-weight: bold;
  display: flex;
}

nav .nav-links ul li {
  list-style: none;
  text-align: center;
  padding: 25px;
  cursor: pointer;
  transition: 0.5s;
}

nav .nav-links ul li a {
  text-decoration: none;
  color: #fff;
}

nav .nav-links ul li:hover {
  background-color: #e3774c;
  color: white;
}

nav .nav-links ul li ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

nav .nav-links ul li li {
  float: left;
}

