header {
  width: 100%;
  height: 100%;
  background-color: rgba(16, 16, 16, 0.4745098039);
}

header .top-border {
  width: 80px;
  height: 80px;
  position: absolute;
  border-top: 5px solid #e3774c;
  border-left: 5px solid #e3774c;
  margin-left: 100px;
  margin-top: 20px;
}

header .bottom-border {
  width: 80px;
  height: 80px;
  position: absolute;
  border-top: 5px solid rgb(236, 97, 47);
  border-left: 5px solid rgb(236, 97, 47);
  margin-left: 250px;
  margin-top: 350px;
  transform: rotate(180deg);
}

header .scrollDown {
  width: 100%;
  position: relative;
  text-align: center;
  z-index: 55;
  cursor: pointer;
  top: 90px;
  animation: anim 0.6s ease alternate infinite;
}

header .scrollDown svg {
  width: 70px;
  height: 70px;
  fill: #fff;
}

@keyframes anim {
  from {
    top: 220px;
  }

  to {
    top: 190px;
  }
}

header .avatar_container {
  display: flex;
  position: absolute;
  width: 300px;
  height: 350px;
  z-index: -0.5;
  background: #fff;
  margin-top: 50px;
