@font-face {
  font-family: "Ingrid Darling", cursive;
  src: url("../fonts/DarlingtonDemo-z8xjG.ttf");
}

@font-face {
  font-family: NSABold;
  src: url("../fonts/NotoSansArmenian-Bold.ttf");
}

@font-face {
  font-family: NSARegular;
  src: url("../fonts/NotoSansArmenian-Regular.ttf");
}

@font-face {
  font-family: NSAMedium;
  src: url("../fonts/NotoSansArmenian-Medium.ttf");
}

@font-face {
  font-family: NSALight;
  src: url("../fonts/NotoSansArmenian-Light.ttf");
}

@font-face {
  font-family: NSAThin;
  src: url("../fonts/NotoSansArmenian-Thin.ttf");
}

body {
  font-family: NSARegular, "Roboto", sans-serif;
  background-color: rgba(26, 26, 26, 0.3725490196);
  z-index: 1;
}

body::-webkit-scrollbar {
  width: 10px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background-color: #ccc;
}

body::-webkit-scrollbar-thumb {
  background: #e3774c;
}

@media screen and (max-width: 1930px) {
  header .top-border {
    margin-left: 289px;
  }

  header .avatar_container {
    margin-top: 200px;
  }
}

@media screen and (max-width: 1920px) {
  main .top-border {
    margin-left: 451px;
  }

  main .second_site .border .bottom-border2 {
    margin-right: 422px;
  }

  header .top-border {
    margin-left: 290px;
    margin-top: 170px;
  }

  header .bottom-border {
    margin-top: 500px;
  }

  header .description_container p {
    margin-top: 150px;
  }

  header .description_container h1 {
    margin-top: 150px;
  }

  main .top-border {
    margin-left: 451px;
  }

  main .second_site .border .bottom-border2 {
    margin-right: 422px;
  }
}